<template>
  <div class="venue-container">
    <div class="title">
      <div class="name">
        {{ venue.name }}
      </div>
      <div
        class="play-video"
        v-if="venue.video"
        @click="showVideo = !showVideo"
      >
        {{ showVideo ? '关闭视频' : '播放视频' }}
      </div>
      <div class="right" @click="handleGoBack">
        <van-icon name="arrow-left" />
        <div>返回</div>
      </div>
    </div>
    <div class="detail-content">
      <div class="video-container" v-if="showVideo">
        <video
          :src="require(`../../assets/${venue.video}`)"
          height="150px"
          controls
        ></video>
      </div>
      <div class="img-container" v-else>
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(image, index) in venue.images" :key="index">
            <img
              v-lazy="image"
              style="height: 150px;width:300px;margin-top:8px;"
            />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="venue-info">
        <van-cell :value="venue.info" />
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Lazyload } from 'vant'
Vue.use(Lazyload)
export default {
  props: {
    venue: {
      type: Object,
      require: true,
      default: () => {}
    }
  },
  data() {
    return {
      showVideo: false
    }
  },
  methods: {
    handleGoBack() {
      this.$emit('handleGoBack', this.venue)
    }
  }
}
</script>

<style>
.venue-container {
  position: absolute;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #fff;
  z-index: 999;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  /* padding: 8px; */
}
.title {
  color: #1890ff;
  display: flex;
  height: 24px;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 4px 8px;
}
.title :hover {
  cursor: pointer;
}
.right {
  display: flex;
  height: 24px;
  flex-direction: row;
  align-items: center;
}
.detail-content {
  display: flex;
  flex-direction: column;
  margin-top: 4px;
  text-indent: 2em;
}
</style>
